/* Zebra striping */
.order-main tr:nth-of-type(odd) { 
	background: #eee; 
	}

.order-main th { 
	background: #222; 
	color: white; 
	font-weight: bold; 
	}

.order-main td, .order-main th { 
	padding: 20px !important; 
	border: 1px solid #ccc; 
	text-align: left; 
	font-size: 14px;
    font-weight: 500;
	}


@media 
only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px)  {

	.order-main table { 
	  	width: 100%; 
	}

	/* Force table to not be like tables anymore */
	.order-main table, .order-main thead, .order-main tbody, .order-main th, .order-main td, .order-main tr { 
		display: block; 
	}
	
	/* Hide table headers (but not display: none;, for accessibility) */
	.order-main thead tr { 
		position: absolute;
		top: -9999px;
		left: -9999px;
	}
	
	.order-main tr { border: 1px solid #ccc; }
	
	.order-main td { 
		/* Behave  like a "row" */
		border: none;
		border-bottom: 1px solid #eee; 
		position: relative;
		padding-left: 50% !important; 
	}

	.order-main td:before { 
		/* Now like a table header */
		position: absolute;
		/* Top/left values mimic padding */
		top: 20px;
		left: 10px;
		width: 45%; 
		padding-right: 10px; 
		white-space: nowrap;
		/* Label the data */
		content: attr(data-column);

		color: #000;
		font-weight: bold;
	}

}

