#get-start-main {
  position: relative;
  border-top: 10px solid #7030a0;
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

#get-start-main:before {
  content: "";
  width: 0;
  height: 0;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-top: 20px solid #7030a0;
  left: 50%;
  top: 0;
  position: absolute;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

#get-start-main:hover:before {
  border-top-color: #fff;
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

#get-start-main:hover {
  border-color: #fff;
  background: #7030a0;
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.get-start-box-inner {
  min-height: 100px;
  display: flex;
  align-items: center;
}

.get-start-box-inner h2 {
  position: relative;
  font-size: 55px;
  font-weight: bold;
  color: #7030a0;
}

#get-start-main:hover .get-start-box-inner h2 {
  color: #fff;
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.get-start-box-inner h4 {
